import { forwardRef } from 'react';

import { Wrapper, ImageWrapper, Text, Sponsored } from './Item.styles';
import { ItemProps } from './Item.types';

const RelatedArticlesCarouselItem = forwardRef<HTMLAnchorElement, ItemProps>(
  function RelatedArticlesCarouselItem(
    { href, title, image, isSponsored = false, ...rest },
    ref,
  ) {
    return (
      <Wrapper {...rest} ref={ref} href={href}>
        <ImageWrapper>{image}</ImageWrapper>
        <Text>
          {isSponsored && <Sponsored>sponsored</Sponsored>}
          {title}
        </Text>
      </Wrapper>
    );
  },
);

export default RelatedArticlesCarouselItem;
